/* Apply styles to the container holding the video background and content */
.bmi-calculator-container {
    position: relative;
    max-width: 100%;
    height: 100;
    overflow: hidden;
    margin: 0; /* Remove margin to fill the entire screen */
  }
  
  /* Style the video background */
  .video-background {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    opacity: 0.7;
  }
  
  /* Style the content container on top of the video */
  .content {
    position: relative;
    z-index: 1;
    background: rgba(255, 255, 255, 0.8); /* Add a semi-transparent white background with more opacity */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(110, 78, 78, 0.2); /* Add a subtle box shadow */
    transition: background 0.3s ease-in-out; /* Smooth background color transition */
  }
  
  /* Style the heading */
  h2 {
    font-size: 32px; /* Increase font size */
    font-family: 'Times New Roman', Times, serif;
    color: #333; /* Darken the text color */
    margin-bottom: 20px; /* Slightly reduce margin */
    text-align: center;
    padding: 10px;
  }
  
  /* Style the form labels and input fields */
  form label {
    display: block;
    margin-top: 15px; /* Increase top margin */
    font-size: 20px;
    font-family: 'Times New Roman', Times, serif;
    color: #555; /* Adjust label color */
  }
  
  input,
  select {
    width: 100%;
    margin-bottom: 20px; /* Increase bottom margin */
    padding: 10px; /* Increase padding */
    font-size: 18px; /* Adjust font size */
    border: 1px solid #ddd;
    border-radius: 10px;
    transition: border-color 0.3s ease-in-out; /* Smooth border color transition */
  }
  
  input:focus,
  select:focus {
    border-color: #218838; /* Change border color on focus */
  }
  
  /* Style the button container to center and move down the button */
  .button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  /* Style the button with a gradient background */
  button {
    background: linear-gradient(to right, #6f42c1, #218838); /* Gradient background */
    color: #fff;
    padding: 12px 20px; /* Adjust padding */
    font-size: 20px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    text-align: center;
    transition: background 0.3s ease-in-out; /* Smooth background color transition */
  }
  
  button:hover {
    background: linear-gradient(to right, #218838, #6f42c1); /* Change gradient on hover */
  }
  
  /* Style the error message */
  .error-message {
    color: #dc3545;
    margin-top: 15px; /* Adjust margin */
  }
  
  /* Style the result messages */
  p {
    margin-top: 30px; /* Adjust margin */
    font-size: 20px; /* Adjust font size */
    color: #333;
  }
  