/* DietPlan.css */

/* Main Diet Plan Container */
.diet-plan {
    border: 3px solid #ff6f61;
    padding: 30px;
    margin: 30px 0;
    border-radius: 20px; /* Slightly increase border radius for a softer look */
    background-color: #f8f8f8; /* Use a lighter background color */
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease-in-out;
}

.diet-plan:hover {
    background-color: #f2f2f2; /* Lighten background color on hover */
}

/* Meal Plan Container */
.meal-plan {
    display: block;
}

/* Individual Meal Category */
.meal-category {
    border-radius: 20px;
    overflow: hidden;
    transition: box-shadow 0.3s ease-in-out;
    padding: 30px;
    margin-bottom: 30px;
    font-family: 'Poppins', sans-serif;
    color: #333; /* Slightly darker text color for better contrast */
    background-color: #ffffff; /* White background for meal categories */
}

.meal-category:hover {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

/* Meal Category Title */
.meal-category h3 {
    color: #ff6f61;
    font-size: 1.8em; /* Increase font size for emphasis */
    margin-bottom: 15px;
}

/* Meal List Styling */
ul {
    list-style-type: none;
    padding: 0;
}

li {
    margin-bottom: 15px;
    font-size: 1.3em; /* Slightly increase font size */
    color: #555;
}

/* Error Message Styling */
.error-message {
    color: #ff6f61;
    margin-top: 20px;
    font-weight: bold;
    font-size: 1.4em; /* Increase font size for emphasis */
}